<template>
  <div id="vue">
    <!--DOM-->
    <div v-bind:title="msg">{{message}}</div>

    <!---->
    <div v-if="seen">true</div>
    <div v-else>false</div>
    <!---->
    <template v-if="seen">
      <h1></h1>
    </template>
    <template v-else>
      <h1></h1>
    </template>
    <!--v-showcssdisplay-->
    <div v-show="seen">show</div>

    <!---->
    <ul>
      <li v-for="(todo,index) in todos" v-bind:key="index" v-bind:title="todo.text">
        {{index}} - {{todo.text}}
      </li>
    </ul>
    <!--json-->
    <div v-for="(value,key,index) in classObject">
      {{index}} - {{key}} -- {{value}}
    </div>
    <!---->
    <div v-on:click="reverseMessage">{{message}}</div>

    <!--v-model-->
    <input type="text" v-model="message">
    <p>{{message}}</p>

    <!--v-once-->
    <p v-once>{{message}}</p>

    <!--HTML,XSS-->
    <div v-html="rawHtml"></div>

    <!--v-bind-->
    <p v-bind:id="vId">222</p>

    <!--js-->
    <ul>
      <li>{{num+1}}</li>
      <li v-bind:id="'list-'+vId">{{bok?'':''}}</li>
    </ul>

    <!--
      MathDate
    -->
    <div>{{new Date().getTime()}}</div>

    <!--, .prevent event.preventDefault()-->
    <form action="" v-on:submit.prevent="onSubmit"></form>

    <!--vuev-bind::-->
    <a :href="href"></a>

    <!--v-on@-->
    <a href="javascript:;" @click="reverseMessage">{{message}}</a>

    <!--
    class
    
    class
    -->
    <div v-bind:class="{active:isActive}">class</div>
    <!---->
    <div v-bind:class="classObject">class</div>
    <!---->
    <div v-bind:class="classObj"></div>

    <!--style,class-->
    <div v-bind:style="{color:activeColor,fontSize:fontSize}"></div>
  </div>
</template>

<script>
  /* eslint-disable */
export default{
  name:"vue",
  //data
  data(){
    return {
      message:'hello world',
      msg:""+new Date().toLocaleString(),
      seen:true,
      isActive:true,
      error:false,
      todos:[
        {text:'js',id:1},
        {text:'java',id:2},
        {text:'css',id:3}
      ],
      rawHtml:'<span style="color:red;">1122233344</span>',
      vId:"123",
      num:234,
      bok:false,
      href:"http://www.baidu.com",
      classObject:{
        active:true,
        'text-danger': false
      },
      activeColor:'red',
      fontSize:'30px'
    }
  },
  //
  methods:{
      reverseMessage:function(){
          this.message = this.message.split('').reverse().join('');
      }
  },
  //
  computed:{
      classObj(){
        return {
          active: this.isActive && !this.error,
          'text-danger': !this.error
        }
      }
  },
  /**
   * 
   *
   */
  beforeCreate: function () {
    console.group('beforeCreate ===============');
    console.log("%c%s", "color:red" , "el     : " + this.$el); //undefined
    console.log("%c%s", "color:red","data   : " + this.$data); //undefined
    console.log("%c%s", "color:red","message: " + this.message); //undefined
  },
  created: function () {
    console.group('created ===============');
    console.log("%c%s", "color:red","el     : " + this.$el); //undefined
    console.log("%c%s", "color:red","data   : " + this.$data); //
    console.log("%c%s", "color:red","message: " + this.message); //
  },
  beforeMount: function () {
    console.group('beforeMount ===============');
    console.log("%c%s", "color:red","el     : " + (this.$el)); //undefined
    console.log("%c%s", "color:red","data   : " + this.$data); //
    console.log("%c%s", "color:red","message: " + this.message); //
  },
  mounted: function () {
    console.group('mounted ===============');
    console.log("%c%s", "color:red","el     : " + this.$el); //
    console.log(this.$el);
    console.log("%c%s", "color:red","data   : " + this.$data); //
    console.log("%c%s", "color:red","message: " + this.message); //
  },
  beforeUpdate: function () {
    console.group('beforeUpdate ===============');
    console.log("%c%s", "color:red","el     : " + this.$el);
    console.log(this.$el);
    console.log("%c%s", "color:red","data   : " + this.$data);
    console.log("%c%s", "color:red","message: " + this.message);
  },
  updated: function () {
    console.group('updated ===============');
    console.log("%c%s", "color:red","el     : " + this.$el);
    console.log(this.$el);
    console.log("%c%s", "color:red","data   : " + this.$data);
    console.log("%c%s", "color:red","message: " + this.message);
  },
  beforeDestroy: function () {
    console.group('beforeDestroy ===============');
    console.log("%c%s", "color:red","el     : " + this.$el);
    console.log(this.$el);
    console.log("%c%s", "color:red","data   : " + this.$data);
    console.log("%c%s", "color:red","message: " + this.message);
  },
  destroyed: function () {
    console.group('destroyed ===============');
    console.log("%c%s", "color:red","el     : " + this.$el);
    console.log(this.$el);
    console.log("%c%s", "color:red","data   : " + this.$data);
    console.log("%c%s", "color:red","message: " + this.message)
  }
}
</script>

<style>

</style>



